}

.team {
  background-color: #f5f5f5;
  min-height: 820px;
}

.team__text p {
  color: #333333;
}

.team__text {
  text-align: center;
  margin: 140px auto 130px;
}

.team__text span {
  color: #fc6f4e;
  font-size: 50px;
}

.team__text p {
  font-size: 50px;
}

.team__card {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  width: 360px;
  height: 450px;
  margin: 15px;
}

.card:hover {
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
}

.card__right {
  background-color: #ff5c36;
}

.card__left {
