  font-size: 16px;
}

.card__right-in span {
  font-size: 30px;
}

.card__right-in p {
  font-size: 16px;
}

.card__center {
  width: 360px;
  height: 450px;
  background-color: #fff3f0;
}

.card__center-flex {
  margin-top: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card__center-line-text {
  font-size: 10px;
  margin-bottom: 10px;
}

.card__center-skill {
  margin: 5px;
