}

.clients {
  min-height: 650px;
}

.clients__slider-side1-text-top {
  margin-top: 60px;
}

.clients__slider-side-head {
  width: 100px;
  height: 100px;
  border-radius: 70px;
  border: 1px solid #cfcfcf;
  margin: -50px auto 0px;
  background-color: white;
}

.clients__text p {
  color: #333333;
}

.clients__text {
  text-align: center;
  font-size: 16px;
  margin-top: 130px;
}

.clients__text span {
  color: #fc6f4e;
  font-size: 50px;
}

.clients__text p {
  font-size: 50px;
}

.clients__slider {
  margin-top: 30px;
}

.clients__slider-flex {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.clients__slider-side {
  margin: 90px 15px 15px 0px;
  width: 530px;
  height: 270px;
  text-align: center;
  font-family: "Source Sans Proc Regular", sans-serif;
  border: 1px solid #cfcfcf;
}

.clients__slider-side:hover {
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
  border: none;
}

.clients__slider-side1-text-bottom p span {
  font-family: "Source Sans Pro SemiBold", sans-serif;
}

