}

.slider {
  display: flex;
  justify-content: center;
  margin: 40px 15px 0px 0px;
}

.switch {
  margin: 4px;
  width: 18px;
  height: 6px;
  background-color: #e0e0e0;
  border: none;
}

.switch:focus {
  background-color: #ff6e47;
  outline: none;
}

.partners {
  min-height: 200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  background-color: #fbfbfb;
}

.partners__img {
  margin: 30px;
  flex-grow: 1;
}

.footer {
  min-height: 250px;
  display: flex;
  justify-content: center;
  background-color: #f7f7f7;
}

.contacts__button-in {
  width: 240px;
  height: 60px;
  border-radius: 50px;
  background-color: #fdfdfd;
  outline: none;
  border: none;
  box-shadow: 0 8px 10px rgba(0, 0, 0, 0.3);
  font-size: 16px;
  margin-left: 150px;
}

.contacts__link__box {
  width: 50px;
  height: 50px;
  border-radius: 50px;
  margin: 8px;
  background-color: #d3d3d3;
}

.contacs__link {
  display: flex;
  margin-top: 50px;
}

.contacts__link-img {
  display: block;
  padding: 16px 17px 19px 16px;
  margin: 0px;
}

.contacts__link__img-fb {
  display: block;
  padding: 15px 17px 19px 20px;
}

.contacts__link__box:hover {
  background-color: #fc6f4e;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
}

.contacts-text p {
  margin: 25px 0px;
}

